
html {
  font-size: 62.5%;
}
html input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
body {
  font-size: 14px;
}
main.container {
  padding-top: 20px;
}
ol {
  list-style-type: decimal;
}
ul {
  list-style-type: disc;
}
.container {
  padding: 0 15px;
  width: 992px;
  margin: 0 auto;
}
@media (max-width: 992px) {
.container {
    width: 768px;
}
}
@media (max-width: 768px) {
.container {
    width: 100%;
}
}



